<template>
    <div>
        <van-nav-bar
        title="登录"
        left-text="返回"
        left-arrow
        @click-left="onClickLeft"
        />
        <van-form @submit="onSubmit">
            <van-field
                v-model="username"
                name="用户名"
                label="用户名"
                placeholder="用户名"
                :rules="[{ required: true, message: '请填写用户名' }]"
            />
            <van-field
                v-model="password"
                type="password"
                name="密码"
                label="密码"
                placeholder="密码"
                :rules="[{ required: true, message: '请填写密码' }]"
            />
            <div style="margin: 16px;">
                <van-button round block type="info" native-type="submit">提交</van-button>
            </div>
        </van-form>
    </div>
</template>
<script>
import jsSHA from 'jssha'
import {mapMutations} from 'vuex'
export default {
    data(){
        return{
            username:'',
            password:'',
        }
        
    },
    methods:{
        ...mapMutations(['changeLogin']),
        setPw(){
            // let sha256 = require("js-sha256").sha256//这里用的是require方法，所以没用import
            // let Base64= require("js-base64").Base64
            // this.pw=Base64.encode(sha256(this.passWord))
            const shaObj=new jsSHA("SHA-256","TEXT")
            shaObj.update(this.password)
            this.pw=shaObj.getHash("B64")
            console.log(this.pw)
            return this.pw
        },
        onSubmit(){
            let _this=this;
            this.$http({
                method:'post',
                url:'http://117.78.27.3:8080/gout2.0.hw4.stable/gout/login',
                data:{
                    comeFrom:"md",
                    userName:this.username,
                    password:this.setPw(),
                }
            })
            .then(res=>{
                console.log(res);
                _this.MobilePhone=res.data.data.Tel;
                window.localStorage.setItem('MobilePhone',_this.MobilePhone);
                // console.log('jwt '+res.data.data.Token);
                _this.userToken = 'jwt '+res.data.data.Token;
                // 将用户token保存到vuex中
                _this.$store.dispatch('cLogin',_this.userToken);
                // _this.$router.push('/homeuser');
                _this.$toast.success('登录成功');
                this.$router.push({path:'/homeuserlogged'})
            })
            .catch(error=>{
                alert('账号或密码错误');
                console.log(error);
            })
        },
        onClickLeft(){
            this.$router.push({path:'/homeuserlogged'})
        }
    }
    
    // mounted(){
    //     this.$http.post('http://117.78.27.3:8080/gout2.0.hw4.stable/gout/login',{
            
    //         comeFrom:"md",
    //         userName:"lichanggui",
    //         password:this.setPw(),
            
    //     }).then(
    //         response=>{
    //             console.log(response.body)
    //         }
    //     )
    // }
}
</script>


